import back from '../../components/back.vue';
export default {
	data() {
		return {
			pageNameFlag:1,
			showFlag:0,
			pageName:'手机快速注册',
			phoneNum:'',
			resetFlag:0,
			nextFlag:0,
			chooseFlag:1,
			errorMsg:''
		}
	},
	methods: {
		reset(){
			this.phoneNum='';
			this.resetFlag=0;
			this.nextFlag=0
		},
		changePhoneNum(e){
			var that = this;
			if(e.detail.value.length>0){
				that.phoneNum=e.detail.value;
				that.resetFlag=1;
			}else{
				that.phoneNum='';
				that.resetFlag=0;
			}
			if(e.detail.value.length>=10&&that.chooseFlag==1){
				that.nextFlag=1
			}else{
				that.nextFlag=0
			}
		},
		nextPage(){
			uni.navigateTo({
				url:'../fastLoginVercode/fastLoginVercode?phoneNum='+this.phoneNum
			})
		},
		changeChooseFlag(flag){
			var that = this;
			if(flag==2){
				if(that.chooseFlag==1){
					that.chooseFlag=0
				}else{
					that.chooseFlag=1
				}
			}else{
				that.chooseFlag=flag;
			}
			if(that.chooseFlag==0){
				that.nextFlag=0;
				return;
			}
			if(that.chooseFlag==1&&that.phoneNum.length>=10){
				that.nextFlag=1;
				return;
			}
			
		},
		jumpToUserAgreement(){
			uni.navigateTo({
				url: '../userAgreement/userAgreement',
			});
		},
		jumpToPrivacyAgreement(){
			uni.navigateTo({
				url: '../privacyAgreement/privacyAgreement',
			});
		}
		
	},
	onLoad() {
	},
	components:{
		back
	}
}
